// styletitle.scss

.news-page {
	p {
		@include margin(vw(20) null vw(20) null);
		&:first-of-type {
			margin-top: 0;
		}
	}
}

// 
//------------------------------------------------------------

.next-content {
	position: relative;
	@include margin(vw(30) null vw(30) null);
	text-align: center;
}

// 
//------------------------------------------------------------

.news-supply-link {
	text-align: right;
	@include margin(vw(30) null vw(30) null);
	img {
		width: vw(72);
		vertical-align: baseline;
	}
}

// 
//------------------------------------------------------------

// style.scsscar-thumb-list-area
.news-list-area {
	.txt-rank {
		font-size: vw(12);
	}
	.news-thumb-memo {
		margin-bottom: vw(5);
	}
	&.car-thumb-area {
		.car-thumb-list {
			.car-thumb-img {
				height: 19.5vw;
				padding: 0;
			}
			.txt-rank {
				width: 64%;
			}
		}
	}
}
