//=========================
// point
//=========================

.point-balloon {
	@include inline-block;
	position: relative;
	margin-bottom: vw(6);
	padding: vw(5) vw(10);
	border-radius: vw(3);
	background-color: colors(col-blue);
	color: colors(col-white);
	font-size: vw(14);
	font-weight: 700;
	line-height: 1;
	text-align: center;
	&:after {
		position: absolute;
		right: vw(10);
		bottom: vw(-12);
		border: vw(6) solid transparent;
		border-top: vw(6) solid colors(col-blue);
		content: "";
	}
}

